body {
  background-color: #0b9cf0;
  margin: 0;
  padding: 0;
}

.gallery {
  width: 100%;
  height: auto;
  flex-wrap: wrap;
  margin-top: 5px;
  display: flex;
}

.card-link {
  height: auto;
  background-color: #ccc;
  border-radius: 5px;
  flex: 1 0 25%;
  margin: 5px;
  text-decoration: none;
}

.card-link img {
  width: 100%;
  height: 300px;
}

.info {
  width: auto;
  justify-content: center;
  align-items: baseline;
  margin-right: 40px;
  padding-left: 3px;
  display: flex;
}

.info-item {
  margin-right: 25px;
  padding-left: 3px;
  padding-right: 3px;
}

.info-item:nth-child(4) {
  margin-left: 25px;
}

.info-item .titulo {
  font-size: 20px;
  font-weight: bold;
}

.info-item li {
  width: 60px;
  color: #000;
  text-align: center;
  list-style: none;
}

.search-form {
  height: 100px;
  background-color: #069e8a;
  justify-content: center;
  align-items: center;
  display: flex;
}

.entrada {
  width: 500px;
  height: 40px;
  padding-left: 2px;
  font-size: 30px;
}

.boton {
  height: 45px;
  cursor: pointer;
  margin-left: 5px;
  font-size: 30px;
}

.boton:hover {
  background-color: #0bfa17;
}

.entrada:focus {
  outline: 2px solid #09f;
}

.load-more {
  width: 200px;
  height: 50px;
  color: azure;
  text-transform: uppercase;
  cursor: pointer;
  background-color: orange;
  border: 2px solid red;
  border-radius: 5px;
  margin: 30px auto 100px;
  font-family: sans-serif;
  font-size: 20px;
  font-weight: bold;
  transition: all .5s;
}

.load-more:hover {
  background-color: #f79976;
  border-color: #0bfa17;
  transform: scale(1.1);
}

